html{
  height: 100%;
  box-sizing: border-box;
}
body {
  /* font-family: Arial, sans-serif; */
  margin: 0;
  position: relative;
  min-height: 100%;
  padding-bottom: 14rem;
  box-sizing: inherit;
}
.container{
  background-color: black;
}
header,.footer-content{
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: auto;
}

.header-logo img {
  width: 100px;
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
}

nav a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

main {
  margin-top: 4rem;
}

footer {
  background-color: black;
  margin-top: 4rem;
  text-align: center;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
}

footer .footer-logo img {
  width: 50px;
}
.footer-social{
  color: white;
}
.social-icons a {
  margin: 0 10px;
  color: white;
}

.footer-bottom p{
  color: white;
  margin: 0;
}

button {
  background-color: #F6EEE3;
  border: 1px solid black;
  padding: 10px;
  cursor: pointer;
}


/* Home Page*/
.page-container{
  width: 80%;
  margin: auto;
}

.heroPage{
display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #F6EEE3;
  padding: 2rem;
}

.heroPage .profileImg, .heroPage .myName{
  flex: 1;
  display: block;
  margin: auto;
}

.myName h1{
  font-size: 40px;
  margin: 0;
}

.myName p{
  font-size: 18px;
}

.resumeDownload{
  display: flex;
  gap:1rem;
  align-items: center;
}

.resumeDownload button{
  background-color: black;
  color: #F6EEE3;
}

.resumeDownload button:hover{
  background-color: #F6EEE3;
  color: black;
}

.homeAboutMe{
  background-color: #F6EEE3;
  height: 400px;
  padding: 2rem;
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.homeAboutMe p{
 font-size: 18px;
 width: 80%;
 margin: 0;
 line-height: 1.9rem
}

.homeProject{
  background-color: #F6EEE3;
  margin-top: 4rem;
    padding: 2rem;
}

.homeProject h2{
  font-size: 35px;
  margin: 0;
}
.projectSection {
  display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1rem;
    /* margin-bottom: 8rem; */
  margin-top: 2rem;
}

.card{
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: transform 0.3s ease;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 10px;
}

.card img{
  flex:3;
  width: 100%;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.card p{
  margin: 1rem 1rem;
  /* flex:1 */
}

.card:hover{
  transform: scale(1.1);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* About me Page*/
.aboutme-page{
  background-color: #F6EEE3;
  padding: 3rem 0;
}

.aboutme{
  width: 80%;
  margin: auto;
}

.aboutme h2{
  font-size: 35px;
  margin-top: 1rem;
  margin-bottom: 0;
}

.aboutme h3{
  margin-bottom: 0;
}

.aboutme p{
  margin-bottom: 0;
  font-size: 18px;
}

.aboutme  ul li{
  font-size: 18px;
}
/* Contact Page */
.contact h2{
  font-size: 35px;
}

.contact p{

}

















@media screen and (max-width:1200px) {
  .projectSection{
      grid-template-columns: 1fr 1fr;
  } 
}


@media screen and (max-width:480px) {
  .heroPage{
    flex-direction: column;
  }

  .profileImg img{
    display: flex;
    margin: auto;
    width: 100%;         /* Adjust the image width to 90% of its container */
    height: auto;  
  }

  .myName h1{
    font-size: 30px;
    margin-top: 2rem;
  }
  
  .myName p{
    margin-top: 0.5rem;
    margin-bottom: 0;
    /* font-size: 18px; */
  }

  .resumeDownload{
    margin-top: 0.5rem;
  }

  .homeAboutMe{
    height: 100%;
    padding: 2rem 0;
  }

  .projectSection{
      grid-template-columns: 1fr;
  } 

  .footer-content{
    flex-direction: column;
  }

  .footer-bottom{
    margin-top: 2rem;
  }
}
